<template>
  <div class="detail_shopinfo" v-if="Object.keys(shopInfo).length !== 0">
    <div class="shop_name">
      <img :src="shopInfo.shopLogo" alt="" />
      <span>{{ shopInfo.name }}</span>
    </div>

    <div class="shop_info">
      <div class="shop_sells">
        <i>{{ shopInfo.cSells | sells }}</i>
        <span>总销量</span>
      </div>
      <div class="shop_goods">
        <i>{{ shopInfo.cGoods }}</i>
        <span>全部宝贝</span>
      </div>
      <div class="score">
        <p
          v-for="(item, index) in shopInfo.score"
          :key="index"
          class="score_item"
        >
          <span class="score_name">{{ item.name }}</span>
          <span
            class="score_point"
            :style="item.isBetter ? 'color:red' : 'color:green'"
            >{{ item.score }}</span
          >
          <span
            class="score_h"
            :style="
              item.isBetter ? 'background-color:red' : 'background-color:green'
            "
            >{{ item.isBetter ? "高" : "低" }}</span
          >
        </p>
      </div>
    </div>

    <div class="enter">
        <div class="enter_shop">进店逛逛</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    shopInfo: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  filters: {
    sells: function (value) {
      let result = value;
      if (value >= 10000) {
        result = (value / 10000).toFixed(1) + "万";
      }
      return result;
    },
  },
};
</script>

<style scoped>
.detail_shopinfo {
  padding: 10px 10px;
  border-bottom: 5px solid #f2f5f8;
}
.shop_name img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.shop_name span {
  vertical-align: middle;
  display: inline-block;
  margin-left: 10px;
  font-size: 14px;
  color: #666;
  letter-spacing: 1px;
}
.shop_info {
  display: flex;
  margin-top: 10px;
  align-items: center;
  position: relative;
}
.shop_info div {
  line-height: 28px;
}
.shop_sells,
.shop_goods {
  margin-left: 20px;
}
.shop_goods {
  margin-right: 25px;
  padding-right: 15px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.shop_sells i,
.shop_sells span,
.shop_goods i,
.shop_goods span {
  display: block;
  font-size: 18px;
  color: #333;
  text-align: center;
}
.shop_sells span,
.shop_goods span {
  font-size: 12px;
}
.score_item {
  font-size: 13px;
  color: #333;
  margin-bottom: 5px;
}
.score_name {
  margin-right: 10px;
}
.score_point {
  display: inline-block;
  width: 26px;
  text-overflow: ellipsis;
  overflow-wrap: normal;
  margin-right: 8px;
}
.score_h {
  display: inline-block;
  color: #fff;
  height: 15px;
  line-height: 15px;
  padding: 1px 3px;
  border-radius: 2px;
}
.enter{
  margin-top: 10px;
  text-align: center;
}
.enter_shop {
  display: inline-block;
  font-size: 14px;
  background-color: #f2f5f8;
  width: 150px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 10px;
}
</style>