import request from "./request"

/**
 * 获取商品详情数据
 */
export function getDetailData(iid) {
    return request({
        url: `/detail`,
        params: {
            iid
        }
    })
}

/**
 * 获取热门推荐数据
 */
export function getRecommend() {
    return request({
        url: `/recommend`
    })
}


/**
 * 获取商品基本信息
 */
export class getGoodsDetailBaseInfo { //商品基本信息类
    constructor(shopInfo) {
        this.title = shopInfo.itemInfo.title
        this.price = shopInfo.itemInfo.price
        this.lowNowPrice = shopInfo.itemInfo.lowNowPrice
        this.oldPrice = shopInfo.itemInfo.oldPrice
        this.discountDesc = shopInfo.itemInfo.discountDesc
        this.discountBgColor = shopInfo.itemInfo.discountBgColor
        this.columns = shopInfo.columns
        this.services = shopInfo.shopInfo.services
    }
}