 * @test: test font
 * @msg: 
 * @param: 
 * @return: 
-->
<template>
  <div class="detail">
    <DetailNavBar @navClick="navClick" ref="detailNav"></DetailNavBar>
    <Scroll class="content" ref="scroll" :probeType="3" @scrollPositio="scrollPositio">
  
      <!-- <ul>
        <li v-for="(item,index) in $store.state.cartList" :key="index">
          {{item}}
        </li>
      </ul> -->
      <DatailSwiper :banners="banners"></DatailSwiper>
      <DetailBaseInfo :goodsBaseInfo="goodsBaseInfo"></DetailBaseInfo>
      <DetailShopInfo :shopInfo="shopInfo"></DetailShopInfo>
      <DetailShowImgs :goodsShowImgs="goodsShowImgs"></DetailShowImgs>
      <DetailGoodsParams
        :goodsParams="goodsParams"
        ref="goodsParams"
      ></DetailGoodsParams>
      <DetailGoodsRate :rate="rate" ref="goodsRate"></DetailGoodsRate>
      <DetailRecommend
        :recommend="recommends"
        ref="goodsRecommend"
      ></DetailRecommend>
    </Scroll>
    <BackTop @click.native="backtop" v-show="isShowBackTop" class="back_top"></BackTop>
    <DetailBottomUntil @addCart="addCart"></DetailBottomUntil>
    <Toast></Toast>
  </div>
</template>

<script>
import Scroll from "@/components/scroll/Scroll";

import DetailNavBar from "./childrenComps/DetailNavBar";
import DatailSwiper from "./childrenComps/DetailSwiper";
import DetailBaseInfo from "./childrenComps/DetailBaseInfo";
import DetailShopInfo from "./childrenComps/DetailShopInfo";
import DetailShowImgs from "./childrenComps/DetailShowImgs";
import DetailGoodsParams from "./childrenComps/DetailGoodsParams";
import DetailGoodsRate from "./childrenComps/DetailGoodsRate";
import DetailRecommend from "./childrenComps/DetailRecommend";
import DetailBottomUntil from "./childrenComps/DetailBottomUntil";

import Toast from "@/components/toast/Toast";
import BackTop from "@/components/backtop/BackTop";

import {
  getDetailData,
  getRecommend,
  getGoodsDetailBaseInfo,
} from "@/network/detail";

import { debounce } from "@/until";
import { itemListenMixin } from "@/until/mixin";

export default {
  name: "Detail",
  components: {
    Scroll,
    DetailNavBar,
    DatailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailShowImgs,
    DetailGoodsParams,
    DetailGoodsRate,
    DetailRecommend,
    DetailBottomUntil,
    BackTop,
    Toast,
  },
  mixins: [itemListenMixin],
  data() {
    return {
      iid: null,
      banners: [], //轮播图数据
      goodsBaseInfo: {}, //商品详情数据
      shopInfo: {}, //商店信息数据
      goodsShowImgs: {}, //商品图片展示数据
      goodsParams: {}, //商品参数数据
      rate: {}, //商品评论数据
      recommends: [], //热门推荐数据
      itemImgLoad: null,
      moduleOffSetTop: [],
      navLinkageIndex: 0,
      isShowBackTop: false,
    };
  },
  mounted() {
    console.log(this.$toast)
  },
  created() {
    console.log(itemListenMixin);
    this.iid = this.$route.params.iid;
    getDetailData(this.iid).then((res) => {
      console.log(res);
      //获取轮播图数据
      this.banners = res.result.itemInfo.topImages;

      //获取商品基本信息
      this.goodsBaseInfo = new getGoodsDetailBaseInfo(res.result);

      //获取商店信息
      this.shopInfo = res.result.shopInfo;

      //获取商品图片展示
      this.goodsShowImgs = res.result.detailInfo;

      //获取商品参数数据
      this.goodsParams = res.result.itemParams;

      //获取商品评论数据
      if (res.result.rate.cRate !== 0) {
        this.rate = res.result.rate;
      }
    });
    //获取热门推荐数据
    getRecommend().then((res) => {
      console.log(res);
      this.recommends = res.data.list;
    });
  },
  // mounted(){

  // },
  destroyed() {
    this.$bus.$off("imgLoad", this.itemImgLoad);
  },
  methods: {
    navClick(index) {
      // console.log(index);
      this.$refs.scroll.scrollTop(0, -this.moduleOffSetTop[index], 100);
    },
    scrollPositio(position){
      //监听回到顶部按钮显示
      this.isShowBackTop = Math.abs(position.y) >= 610;
      // console.log(this.isShowBackTop)
      // this.ishome_tab_show = Math.abs(position.y) >= this.tabControlOffsetTop

      //监听滚动到哪个模块 联动效果
      /**
       * moduleOffSetTop:[0, 5158, 5818, 6050, __ob__: Observer]
       * 0 0-5158之间
       * 1 5158-5818之间
       * 2 5818-6050之间
       * 3 超过6050
       */
      this.navLinkage(position.y)
    },
    navLinkage(position){
      // console.log(-position)
      for(let i=0; i<this.moduleOffSetTop.length; i++){
        if(this.navLinkageIndex !==i && i<this.moduleOffSetTop.length-1 && this.moduleOffSetTop[i]<=(-position) && this.moduleOffSetTop[i+1]>=(-position) || this.navLinkageIndex !==i && i === this.moduleOffSetTop.length-1 && (-position) >= this.moduleOffSetTop[i]){
          // console.log(i)
          // console.log(this.navLinkageIndex)
          this.navLinkageIndex = i
          this.$refs.detailNav.currentIndex = i
        }
      }
    },
    backtop(){ //回到顶部
      this.$refs.scroll.scrollTop(0, 0, 300);
    },
    addCart(){ //加入购物车事件处理
      let addGoodsInfo = {}
      addGoodsInfo.imges = this.goodsShowImgs.detailImage[0].list[0]
      addGoodsInfo.title = this.goodsBaseInfo.title
      addGoodsInfo.price = this.goodsBaseInfo.lowNowPrice
      addGoodsInfo.iid = this.iid
      console.log(addGoodsInfo)
      this.$store.dispatch('addCart',addGoodsInfo).then(res=>{
        this.$toast.showToast(res,1500)
      })
    }
    
  },
};
</script>

<style scoped>
.detail {
  height: 100vh;
  position: relative;
  z-index: 9;
  background-color: #fff;
}
.content {
  height: calc(100% - 44px - 48px);
  overflow: hidden;
}
.back_top{
  bottom: 110px;
}
</style>