
<template>
  <NavBar>
    <div slot="nav_left" class="detail_back iconfont" @click="detailBack">&#xe509;</div>
    <div slot="nav_center" class="detail_nav">
      <div
        v-for="(item, index) in nav_list"
        :key="index"
        class="detail_nav_item"
        :class="{ active: currentIndex === index }"
        @click="detailNavClick(index)"
      >
        {{ item }}
      </div>
      
    </div>
    <div slot="nav_right" class="cart_img" @click="cart">
      <img src="../../../assets/img/common/cart.png" alt="">
    </div>
  </NavBar>
</template>

<script>
import NavBar from "@/components/navBar/common/NavBar";
export default {
  components: {
    NavBar,
  },
  data() {
    return {
      nav_list: ["商品", "参数", "评论", "推荐"],
      currentIndex: 0,
    };
  },
  methods: {
    detailNavClick(index) {
      this.currentIndex = index;
      this.$emit('navClick',index)
    },
    detailBack(){
      this.$router.go(-1)
    },
    cart(){
      this.$router.push({
        name: '购物车'
      })
    }
  },
};
</script>

<style scoped>
.detail_nav {
  display: flex;
}
.detail_nav_item {
  flex: 1;
  text-align: center;
}
.active {
  color: #ff5777;
}
.detail_back{
  font-size: 28px;
  text-align: center;
  transform: rotateY(180deg);
}
.cart_img{
  text-align: center;
}
.cart_img img{
  width: 28px;
  height: 24px;
}
</style>