<!--
 * @name: 
 * @test: test font
 * @msg: 
 * @param: 
 * @return: 
-->
<template>
  <div class="rate_info" v-if="Object.keys(rate).length !== 0">
    <div class="rate_title">
      <span>用户评论</span>
      <i>({{rate.cRate}})</i>
      <span class="rate_more">更多</span>
      <i class="iconfont rate_more_icon">&#xe507;</i>
    </div>

    <div class="rate_user">
      <div class="user_info">
        <img :src="rate.list[0].user.avatar" alt="">
        <strong class="uname">{{rate.list[0].user.uname}}</strong>
      </div>
      <div class="rate_content">
        {{rate.list[0].content}}
      </div>
      <div class="rate_params">
        <span class="time">{{rate.list[0].created | showDate}}</span>
        <span class="goods_params">{{rate.list[0].style}}</span>
      </div>
    </div>
  </div>

  <div v-else class="rate_info">
    <div class="rate_title">
      <span>用户评论</span>
    </div>
    <div class="rate_content">
      暂无评论
    </div>
    <div class="handel">
      <span class="askAll">问大家</span>
      <span class="tips">宝贝好不好，问问已买过的人</span>
    </div>
  </div>
</template>

<script>
import { formatDate } from "@/until"
export default {
  props:{
    rate:{
      type: Object,
      default(){
        return {}
      }
    }
  },
  filters:{
    showDate: function(value){
      let date = new Date(value * 1000)

      return formatDate(date,'yyyy-MM-dd')
    }
  }
}
</script>

<style scoped>
.rate_info{
  padding: 10px 10px;
  border-bottom: 5px solid #f2f5f8;
}
.rate_title{
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid rgba(0,0,0,.1);
  position: relative;
}
.rate_more{
  position: absolute;
  right: 10px;
  font-size: 13px;
  font-style: normal;
  margin-right: 10px;
}
.rate_more_icon{
  position: absolute;
  right: 0;
  font-size: 16px;
}
.rate_user{
  padding: 10px 10px;
}
.user_info img{
  width: 42px;
  height: 42px;
  border-radius: 50%;
  margin-right: 20px;
}
.uname{
  font-size: 15px;
}
.rate_content{
  padding: 10px 12px;
  font-size: 14px;
  color: #777;
}
.rate_params{
  padding: 0px 12px;
  font-size: 12px;
  color: #999;
}
.time{
  margin-right: 10px;
}
.askAll{
  display: inline-block;
  font-size: 12px;
  margin-left: 12px;
  padding: 0px 3px;
  background: #ff5777;
  color: #fff;
}
.tips{
  font-size: 11px;
  color: rgb(153,153,153);
  margin-left: 5px;
}

</style>